import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import topbar from '../component/topbar.vue'
import myProject from '../component/my_project.vue'
import pool from '../component/pool.vue'


export default new Router({
  mode: 'history',
  routes: [
    {path: '/pool', name: "pool", component: pool},
    {path: '/myProject', name: "myProject", component: myProject},
    {path: '/', component: tobar},
  ]
})


